/**
 * 创建空精灵，作为lottie角色的位置参考
 */
import Tiny from 'tiny';
;
import Config from '../config';
export default class treasureBox extends Tiny.Container {
    get currentPosition() {
        return this.sprite.position;
    }
    get bounds() {
        const { x, y } = this.currentPosition;
        const { width, height } = this.sprite;
        return {
            x: x - width / 2,
            y: y - height / 2,
            width,
            height,
        };
    }
    constructor(config) {
        super(config);
        this._originPosition = null;
        this.onUpdate = config.onUpdate || function () { };
        this.onComplete = config.onComplete || function () { };
        this.treasureBoxLottie = config.treasureBoxLottie;
        this.init();
    }
    init() {
        // 创建空精灵
        const sprite = new Tiny.Sprite();
        // 设置宽高，会影响到碰撞检测体积
        sprite.width = 150;
        sprite.height = 150;
        sprite.setAnchor(0.5);
        this.addChild(sprite);
        this.sprite = sprite;
        // 运行状态
        this.running = false;
        // 是否暂停
        this.stopped = false;
        // 定时器
        this.ticker = new Tiny.ticker.Ticker();
        this.ticker.add(() => {
            this.updatePath();
        });
        this._originPosition = {
            x: Config.AppConfig.renderSize.width / 2,
            // 计算屏幕分辨率下，宝箱位置
            y: -(sprite.height / 2 + 20),
        };
        this.moveX = this._originPosition.x;
        this.moveY = this._originPosition.y;
        this.vr = 0;
        this.originPosition = Object.assign({}, this._originPosition);
        this.sprite.setPosition(this.originPosition.x, this.originPosition.y);
    }
    // 更新
    updatePath() {
        const self = this;
        // 幅度
        const M = 100;
        // 周期
        const n = 4;
        // 下落总时间
        const t = Config.GameConfig.treasureBoxCycle * 1000;
        // 弧度，17为浏览器刷新时间，60fps情况下，如果为30fps，需要17*2
        this.vr += n * Math.PI / t * 17;
        // 入参为弧度，出参为[-1,1]，x值变化
        this.moveX = Config.AppConfig.renderSize.width / 2 + Math.sin(-this.vr) * M;
        this.moveY += Config.AppConfig.renderSize.height / t * 17;
        // 到达到屏幕高度70%，播放闪烁动画，并且停止运动
        if (this.moveY > Config.AppConfig.renderSize.height * 0.7) {
            this.onComplete();
            this.ticker.stop();
            // window.cancelAnimationFrame(this.requestAnimationFrame);
        }
        // 创建新路径动画
        const action = Tiny.MoveTo(10, Tiny.point(this.moveX, this.moveY));
        action.onUpdate = function (tween) {
            if (self.running) {
                self.sprite.setPosition(tween.x, tween.y);
                self.onUpdate({ x: tween.x, y: tween.y });
            }
        };
        this.sprite.action = action;
        this.sprite.runAction(action);
        this.running = true;
        this.stopped = false;
    }
    /**
     * 生成计时器
     */
    calculatePath() {
        this.ticker.start();
        // this.requestAnimationFrame = window.requestAnimationFrame();
        this.updatePath();
    }
    /**
     * 开始移动
     */
    startMove() {
        this.calculatePath();
    }
    reset() {
        // 游戏stop
        const x = Config.AppConfig.renderSize.width / 2;
        const y = -(this.sprite.height / 2 + 20);
        this.sprite.setPosition(x, y);
        this._originPosition = { x, y };
        this.originPosition = Object.assign({}, this._originPosition);
        this.moveX = this._originPosition.x;
        this.moveY = this._originPosition.y;
        this.vr = 0;
        this.running = false;
        this.stopped = false;
        // window.cancelAnimationFrame(this.requestAnimationFrame);
        this.ticker.stop();
    }
    pause() {
        // 中途暂停
        this.running = false;
        this.stopped = true;
        // window.cancelAnimationFrame(this.requestAnimationFrame);
        this.ticker.stop();
    }
    resume() {
        // 如果没有运行，且被暂停
        if (this.stopped) {
            this.calculatePath();
        }
    }
}
