import Bean from './bean';
import Tiny from 'tiny';
import Config from '../config';
export default class BeanManger extends Tiny.Container {
    get fpsList() {
        return this.allFpsList;
    }
    constructor(config) {
        super(config);
        this.config = config;
        this.resources = config.resources;
        this.dropList = config.dropList;
        this.pea = config.pea;
        this.app = config.app;
        this.onPositionChange = config.onPositionChange || function () { };
        this.onLastOneFinish = config.onLastOneFinish || function () { };
        this.allFpsList = [];
        this.init();
    }
    init() {
        // 后续豆豆雨继续下落
        this._stopFall = false;
        // 下落豆豆id列表
        this._fallingBeanIdList = new Set();
        this.beanList = [];
        // 根据接口下发掉落物品每种类型创建
        this.beanList = this.dropList.map((item) => {
            const { texture } = this.resources[item.iconKey];
            const bean = new Bean(texture, {
                beanProp: {
                    score: item.score,
                    rewardType: item.rewardType,
                },
                resources: this.resources,
                pea: this.pea,
                onComplete: (pea) => {
                    this.onBeanComplete(pea);
                },
                onUpdate: (pea) => {
                    this.onBeanUpdate(pea);
                },
            });
            this.addChild(bean);
            return bean;
        });
    }
    /**
     * 通过计算，避免豆子重叠情况
     * 比较流弊思路
     */
    _calcPos(beanWidth, beanHeight) {
        const ww = Config.AppConfig.renderSize.width;
        const beanRealWidth = beanWidth / 2;
        const branRealHeight = beanHeight / 2;
        const _pInfo = Array.from(this._fallingBeanIdList)
            .map((bean) => {
            // 过滤位置小于豆子高度的豆子，并记录位置信息
            if (bean.y < branRealHeight) {
                return [bean.x, bean.x + bean.width];
            }
            return null;
        })
            .filter((pos) => pos && pos[0] && pos[0] > 0 && pos[1] - pos[0] >= beanRealWidth)
            // 按照位置信息，从小到大排序
            .sort((a, b) => {
            // 两点相交
            if ((a[0] < b[0] && a[1] > b[0]) || (a[0] > b[0] && a[0] < b[1])) {
                if (a[0] > b[0]) {
                    return 1;
                }
                else {
                    return -1;
                }
            }
            else if (a[1] < b[0]) {
                return -1;
            }
            else if (a[1] > b[0]) {
                return 1;
            }
            else {
                return 0;
            }
        });
        // 如果没有找到会重叠的元素，则随机一个任意位置
        if (!_pInfo.length) {
            const x = Math.floor(Math.random() * (ww - beanRealWidth)) + beanRealWidth;
            return [x, -branRealHeight];
        }
        let posx = 0;
        // 在重叠元素中查找位置放置
        for (let i = 0; i < _pInfo.length && _pInfo.length >= 2; i++) {
            const cur = _pInfo[i];
            const next = _pInfo[i + 1];
            // 如果两个元素之间的位置合适放置，则计算随机坐标
            // 两个节点x范围无相交且，距离宽度可以放下另一个节点
            if (cur && next && cur[1] < next[0] && next[0] - cur[1] > beanRealWidth) {
                posx = (cur[1] +
                    (next[0] - cur[1] - beanRealWidth) * Math.random()).toFixed(2);
            }
        }
        // 如果没找到插值位置，则在头尾处查找
        if (posx === 0) {
            const first = _pInfo[0];
            const last = _pInfo[_pInfo.length - 1];
            // 优先找尾部
            if (ww - last[1] > beanRealWidth) {
                posx = (last[1] + (ww - last[1] - beanRealWidth) * Math.random()).toFixed(2);
                // 然后找头部
            }
            else if (first[0] - 0 > beanRealWidth) {
                posx = ((first[0] - 0 - beanRealWidth) * Math.random()).toFixed(2);
            }
        }
        // 如果头尾都没找到
        // 或计算出来的宽度超出屏幕外
        // ，则随机任意位置
        if (posx < beanRealWidth || posx > ww - beanRealWidth) {
            posx = Math.floor(Math.random() * (ww - beanRealWidth)) + beanRealWidth;
        }
        return [posx, -branRealHeight];
    }
    /**
     * 当豆子到达屏幕底部后，重置豆子状态
     * @param {Bean} bean 豆子实例
     */
    onBeanComplete(bean) {
        this._fallingBeanIdList.delete(bean);
        // 移除动画
        bean.removeActions();
        // 重置豆子状态
        bean.reset();
        this.beanList.push(bean);
        // 开始新豆子动画
        if (this._timer && this._fallingBeanIdList.size === 0) {
            this._timer.destroy();
            this.onLastOneFinish();
        }
    }
    onBeanUpdate(bean) {
        this.onPositionChange(bean);
    }
    showByIndex() {
        const bean = this.beanList.shift();
        const pos = this._calcPos(bean.width, bean.height);
        bean.setPosition(pos[0], pos[1]);
        this._fallingBeanIdList.add(bean);
        bean.start();
    }
    /**
     * 开始下落
     */
    startUp() {
        const { gameTime } = Config.GameConfig;
        const instance = this;
        let tick;
        const duration = gameTime * 1000;
        // 如果掉落间隔可被整除，就将间隔减小些，保证所有掉落物都能完整掉落
        if (duration % this.beanList.length === 0) {
            tick = Math.floor(duration / this.beanList.length - (duration / this.beanList.length) / 10);
        }
        else {
            tick = Math.floor(duration / this.beanList.length);
        }
        const onStep = () => {
            instance.showByIndex();
            instance.allFpsList.push(instance.app.getCurrentFPS());
        };
        // 清除已有定时器
        if (this._timer) {
            this._timer.destroy();
        }
        this._timer = new Tiny.ticker.CountDown({
            duration: tick,
            times: this.beanList.length,
        });
        this._timer.on('update', () => {
            onStep();
        });
        this._timer.start();
        // this.step(tick, duration, onStep, onEnd);
    }
    pause() {
        Array.from(this._fallingBeanIdList.values()).forEach((bean) => bean.pause());
    }
    stopFall() {
        if (this._timer) {
            this._timer.stop();
            this._timer.destroy();
        }
        this._stopFall = true;
    }
    resume() {
        if (this._fallingBeanIdList && this._fallingBeanIdList.size) {
            Array.from(this._fallingBeanIdList).map((bean) => {
                bean.resume();
            });
        }
        else {
            this._fallingBeanIdList.clear();
        }
        this._stopFall = false;
    }
    resetPosition() {
        this.beanList.forEach((bean) => bean.reset());
    }
    restart() {
        this.resetPosition();
        this.startUp();
    }
    update(config) {
        this.resources = config.resources;
        this.dropList = config.dropList;
        // 先清除原先的
        this.beanList.forEach((bean) => bean.destroy());
        // 再重新创建豆子实例数组
        this.init();
    }
}
